var React, _getWindowDimensions, useDimensions;

React = require('react');

_getWindowDimensions = function() {
  var height, width;
  width = window.innerWidth;
  height = window.innerHeight;
  return {width, height};
};

useDimensions = function() {
  var dimensions, setDimensions;
  [dimensions, setDimensions] = React.useState(_getWindowDimensions());
  React.useEffect(function() {
    var handleResize;
    handleResize = function() {
      return setDimensions(_getWindowDimensions());
    };
    window.addEventListener('resize', handleResize);
    return function() {
      return window.removeEventListener('resize', handleResize);
    };
  }, []);
  return dimensions;
};

export default useDimensions;
