var React, SearchBar, SearchComponent, div, i, input;

React = require('react');

({div, i, input} = require('react-dom-factories'));

SearchComponent = React.memo(function(props) {
  return div({
    style: {
      alignItems: 'center',
      backgroundColor: '#ff0000',
      display: 'flex',
      height: 40,
      justifyContent: 'center',
      width: 50
    }
  }, props.children);
});

SearchBar = React.memo(function(props) {
  var filter, setFilter, updateFilter;
  [filter, setFilter] = React.useState('');
  updateFilter = function(event) {
    return setFilter(event.target.value);
  };
  return div({
    style: Object.assign({
      backgroundColor: '#ffffff',
      borderRadius: 20,
      display: 'flex',
      height: 40,
      width: 400,
      flexDirection: 'row',
      overflow: 'hidden'
    }, props.style)
  }, React.createElement(SearchComponent, null, i({
    className: 'fa-solid fa-magnifying-glass',
    style: {
      color: '#ffffff',
      fontSize: 30,
      pointerEvents: 'none'
    }
  })), input({
    type: 'text',
    style: {
      border: 'none',
      flex: 1,
      fontSize: 18,
      margin: 0,
      padding: '0px 10px',
      textAlign: 'center'
    },
    onChange: updateFilter,
    placeholder: 'Buscar...',
    value: filter
  }));
});

export default SearchBar;
